<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <h1 class="display-1 font-weight-regular">Manage Awards</h1>
    </v-row>
    <v-row>
      <v-spacer></v-spacer>
      <v-btn color="primary" dark @click="showForm(null)">
        <v-icon left>
          mdi-pencil-plus-outline
        </v-icon>
        Add New Award
      </v-btn>
    </v-row>
    <AwardForm ref="award_form" />

    <v-row>
      <v-data-table
        :page="getCurrentPage"
        :pageCount="getNumberOfPages"
        :headers="getTableHeader"
        :items="getAwards"
        :footer-props="{
          'items-per-page-options': [10, 20, 30, 40, 50, 100]
        }"
        :options.sync="options"
        :server-items-length="getTotalAwards"
        :loading="getLoadingStatus"
        class="elevation-1 col-12"
      >
        <template v-slot:item.action="{ item }">
          <v-btn
            class="ma-1"
            text
            icon
            color="secondary"
            @click="showForm(item.data.attributes.id)"
          >
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>

          <v-btn
            text
            icon
            color="danger"
            @click="showDeleteDialog(item.data.attributes)"
          >
            <v-icon>mdi-trash-can-outline</v-icon>
          </v-btn>
        </template>
      </v-data-table>

      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title>Are you sure ?</v-card-title>
          <v-card-text
            >Do you want to delete `{{ itemToDelete.title }}`?
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" @click="dialogDelete = false">
              <v-icon>mdi-close</v-icon>
              No
            </v-btn>
            <v-btn color="error" @click="deleteItem(itemToDelete.id)">
              <v-icon>mdi-trash-can-outline</v-icon>
              Yes
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import AwardForm from "@/views/award/AwardForm";

export default {
  name: "Certificate",
  components: { AwardForm },
  computed: {
    ...mapGetters("award", [
      "getAward",
      "getAwards",
      "getAwardDeleted",
      "getLoadingStatus",
      "getCurrentPage",
      "getTotalAwards",
      "getNumberOfPages",
      "getTableHeader"
    ])
  },
  data: () => ({
    ...mapState("award", ["award"]),
    dialogDelete: false,
    itemToDelete: {},
    options: {}
  }),
  watch: {
    options: {
      handler() {
        let payload = this.options;
        this.searchAwards(payload);
      }
    },
    deep: true
  },
  methods: {
    ...mapActions("award", ["searchAwards", "deleteAward"]),
    showDeleteDialog(item) {
      this.itemToDelete = item;
      this.dialogDelete = !this.dialogDelete;
    },
    async deleteItem(itemId) {
      await this.deleteAward(itemId);
      if (this.getAwardDeleted == true) {
        this.dialogDelete = false;
      }
    },
    showForm(awardId) {
      this.$refs.award_form.showFormHandler(awardId);
    }
  }
};
</script>

<style scoped></style>
