<template>
  <div>
    <v-row class="mb-5 mt-2">
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Award Information</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-select
                    dense
                    :items="certificates"
                    v-model="certificate_id"
                    item-value="id"
                    item-text="value"
                    label="Certificate*"
                  ></v-select>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :return-value.sync="from_date"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="from_date"
                        required
                        label="From Date*"
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="from_date" no-title scrollable>
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="menu = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.menu.save(from_date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-menu
                    ref="menu_to_date"
                    v-model="menu_to_date"
                    :close-on-content-click="false"
                    :return-value.sync="to_date"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="to_date"
                        required
                        label="To Date*"
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="to_date" no-title scrollable>
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="menu_to_date = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.menu_to_date.save(to_date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Registration Number*"
                    required
                    v-model="registration_number"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Certificate Number*"
                    v-model="certificate_number"
                  ></v-text-field>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="NIC / Passport*"
                    required
                    @keyup="searchStudentByNicOrPassportHandler"
                    v-model="nic_or_passport"
                  ></v-text-field>
                  <div
                    class="blue--text text--darken-2"
                    v-if="this.foundStudent"
                  >
                    {{ this.student.first_name }} {{ this.student.last_name }}
                    <BR /> <strong>Date Of Birth: </strong>
                    {{ this.student.date_of_birth }}
                  </div>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-select
                    :items="grades"
                    v-model="grade"
                    label="Grade*"
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="warning" dark @click="dialog = false">
              <v-icon left>
                mdi-close
              </v-icon>
              Close
            </v-btn>
            <v-btn color="primary" dark @click="awardCreateHandler">
              <v-icon left>
                mdi-content-save
              </v-icon>
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "AwardForm",
  data: () => ({
    menu: false,
    menu_to_date: false,
    dialog: false,
    certificates: [],
    grades: [],
    student: {
      id: null,
      first_name: null,
      last_name: null,
      date_of_birth: new Date().toISOString().substr(0, 10)
    },
    foundStudent: false,
    nic_or_passport: null
  }),
  computed: {
    ...mapGetters("award", ["getAwardCreated", "getAward"]),
    ...mapGetters("student", ["getStudent"]),
    ...mapGetters("certificate", ["getCertificates"]),
    certificate_id: {
      get() {
        return this.$store.state.award.award.certificate_id;
      },
      set(value) {
        this.$store.commit("award/updateCertificateId", value);
      }
    },
    registration_number: {
      get() {
        return this.$store.state.award.award.registration_number;
      },
      set(value) {
        this.$store.commit("award/updateRegistrationNumber", value);
      }
    },
    from_date: {
      get() {
        return this.$store.state.award.award.from_date;
      },
      set(value) {
        this.$store.commit("award/updateFromDate", value);
      }
    },
    to_date: {
      get() {
        return this.$store.state.award.award.to_date;
      },
      set(value) {
        this.$store.commit("award/updateToDate", value);
      }
    },
    certificate_number: {
      get() {
        return this.$store.state.award.award.certificate_number;
      },
      set(value) {
        this.$store.commit("award/updateCertificateNumber", value);
      }
    },
    grade: {
      get() {
        return this.$store.state.award.award.grade;
      },
      set(value) {
        this.$store.commit("award/updateGrade", value);
      }
    }
  },
  methods: {
    ...mapActions("award", ["addAward", "fetchAward"]),
    ...mapActions("student", ["fetchStudentByNicOrPassport"]),
    ...mapActions("certificate", ["searchCertificates"]),
    async awardCreateHandler() {
      await this.addAward();
      if (this.getAwardCreated == true) {
        this.dialog = false;
      }
    },
    async showFormHandler(awardId) {
      this.clearForm();
      if (awardId != null) {
        await this.fetchAward(awardId);
        let award = this.getAward;
        this.nic_or_passport = award.student.nic;
        this.foundStudent = true;
        this.student.first_name = award.student.first_name;
        this.student.last_name = award.student.last_name;
        this.student.date_of_birth = award.student.date_of_birth;
      }
      this.dialog = true;
    },
    clearForm() {
      this.title = null;
    },
    async searchStudentByNicOrPassportHandler($event) {
      this.foundStudent = false;
      let nicOrPassport = $event.target.value;
      await this.fetchStudentByNicOrPassport(nicOrPassport);
      if (this.getStudent) {
        this.foundStudent = true;
        this.student = this.getStudent;
        this.$store.commit("award/updateStudentId", this.student.id);
      }
    }
  },
  async mounted() {
    this.grades = process.env.VUE_APP_GRADES.split(",");
    let payload = {
      page: 1,
      itemsPerPage: 1000,
      sortBy: ["data.attributes.title"],
      sortDesc: [true]
    };
    await this.searchCertificates(payload);
    let certificateList = [];
    this.getCertificates.map(function(data) {
      certificateList.push({
        id: data.data.attributes.id,
        value: data.data.attributes.title
      });
    });
    this.certificates = certificateList;
  }
};
</script>

<style scoped></style>
